.SocialMedia {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background: white;
    border: none;
  }

  .ScPostion {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    span {
      z-index: 2;
      background: #666666;
      border-radius: 50%;
      padding: 0.1em 1em;
      margin-right: .5em;
      a {
        transition: 0.5s all ease-in-out;
        color: azure;
        font-size: 2.5rem;
      }
    }
  }
}

@media only screen and (max-width: 480px){
    .ScPostion{
        span{
            padding: 0.1em .8em !important;
            a{
                font-size: 1.5rem !important
            }
        }
    }
}