@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');

.mainPage {
    margin-top: 0%;
    background-image: url("https://i.ibb.co/VHHz9N8/bgmain.jpg");
    background-size: 100%;
    img{
        margin-top: 8%;
        margin-bottom: 2%;
        border-radius: 50%;
        width: 15%;
    }
    p{
        color: rgb(204, 211, 211);
        font-size: 3rem;
        font-weight: bold;
        font-family: 'Roboto Mono', monospace;
    }
 p:nth-child(2){
     margin-bottom: 0%;
 }
    p:nth-child(3){
        font-size: 1.2rem;
        color: azure;
    }
    .buttonGroup{
        padding-bottom: 8%;
        font-family: 'Roboto Mono', monospace;
        button:nth-child(1){
            padding: 1em  5em!important
          }
    }

}

@media only screen and (max-width: 480px){
    .mainPage{
        background-repeat: no-repeat;
        background-size: cover;
        img{
            margin-top: 20%;
            width: 30%;
        }
        p{
            font-size: 2rem;
        }
        p:nth-child(3){
            font-size: 1rem;
        }
           .buttonGroup{
               button{
                   padding: .5em .5em
               }
               button:nth-child(1){
                padding: .5em 2em !important
               }
           }
        }
} 