@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:500&display=swap);
html ,body {
  margin: 0%;
}



.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.mainPage {
  margin-top: 0%;
  background-image: url("https://i.ibb.co/VHHz9N8/bgmain.jpg");
  background-size: 100%; }
  .mainPage img {
    margin-top: 8%;
    margin-bottom: 2%;
    border-radius: 50%;
    width: 15%; }
  .mainPage p {
    color: #ccd3d3;
    font-size: 3rem;
    font-weight: bold;
    font-family: 'Roboto Mono', monospace; }
  .mainPage p:nth-child(2) {
    margin-bottom: 0%; }
  .mainPage p:nth-child(3) {
    font-size: 1.2rem;
    color: azure; }
  .mainPage .buttonGroup {
    padding-bottom: 8%;
    font-family: 'Roboto Mono', monospace; }
    .mainPage .buttonGroup button:nth-child(1) {
      padding: 1em 5em !important; }

@media only screen and (max-width: 480px) {
  .mainPage {
    background-repeat: no-repeat;
    background-size: cover; }
    .mainPage img {
      margin-top: 20%;
      width: 30%; }
    .mainPage p {
      font-size: 2rem; }
    .mainPage p:nth-child(3) {
      font-size: 1rem; }
    .mainPage .buttonGroup button {
      padding: .5em .5em; }
    .mainPage .buttonGroup button:nth-child(1) {
      padding: .5em 2em !important; } }

.SocialMedia {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none; }
  .SocialMedia:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background: white;
    border: none; }
  .SocialMedia .ScPostion {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; }
    .SocialMedia .ScPostion span {
      z-index: 2;
      background: #666666;
      border-radius: 50%;
      padding: 0.1em 1em;
      margin-right: .5em; }
      .SocialMedia .ScPostion span a {
        -webkit-transition: 0.5s all ease-in-out;
        transition: 0.5s all ease-in-out;
        color: azure;
        font-size: 2.5rem; }

@media only screen and (max-width: 480px) {
  .ScPostion span {
    padding: 0.1em .8em !important; }
    .ScPostion span a {
      font-size: 1.5rem !important; } }

.navbar {
  width: 100%;
  display: flex;
  flex-direction: row;
  padding-bottom: 1% !important;
  box-shadow: none !important; }

.navBlack {
  display: flex !important;
  flex-direction: row !important;
  justify-content: space-between !important;
  align-items: center;
  color: azure !important; }

.about {
  padding-left: 0% !important;
  padding-right: 0% !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
  margin-bottom: 5%; }
  .about .AboutRow {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    justify-content: center;
    align-items: flex-start;
    box-shadow: 0px 2px 5px 16px #e8e8e8; }
  .about .aboutme {
    width: 80%;
    text-align: left;
    margin-top: 2%; }
    .about .aboutme:first-child {
      margin-left: 2%; }
    .about .aboutme h2 {
      font-weight: bold; }
    .about .aboutme p {
      font-weight: 500; }
  .about .basicInfo {
    width: 100%;
    text-align: left;
    margin-top: 2%; }
    .about .basicInfo h2 {
      font-weight: bold; }
    .about .basicInfo .myinfo {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 70%; }
      .about .basicInfo .myinfo p:nth-child(1) {
        font-weight: bold; }
      .about .basicInfo .myinfo p:nth-child(2) {
        font-weight: 500; }

@media only screen and (max-width: 480px) {
  .AboutRow {
    display: flex !important;
    flex-direction: column !important;
    width: 95% !important; }
    .AboutRow .aboutme {
      width: 95%; }
      .AboutRow .aboutme:first-child {
        margin-left: 0%;
        padding-left: 1em; }
    .AboutRow .basicInfo {
      margin-left: 0%;
      margin-left: 0%;
      padding-left: 1em; }
      .AboutRow .basicInfo .myinfo {
        width: 100%; } }

.skillsCont {
  padding-top: 5%; }
  .skillsCont .title {
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    font-size: 2rem;
    margin-bottom: 1em; }

.skills {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2px 5px 16px #e8e8e8; }
  .skills .skillsLabel {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
    .skills .skillsLabel p {
      font-size: 1.2rem;
      color: #78909C;
      font-weight: bold; }
  .skills .progress-bar {
    background: #78909C !important; }

@media only screen and (max-width: 480px) {
  .skills {
    display: grid;
    grid-template-columns: 1fr; } }

.portoflio {
  margin-top: 10%; }
  .portoflio .CategoryCont {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5%; }
    .portoflio .CategoryCont span {
      font-size: 2rem;
      border-radius: 50%; }
      .portoflio .CategoryCont span:hover {
        cursor: pointer; }
      .portoflio .CategoryCont span:visited {
        background: #78909C; }
      .portoflio .CategoryCont span:active {
        background: #78909C; }
      .portoflio .CategoryCont span p {
        padding: .5em .7em;
        margin: 0%; }
  .portoflio .projectCont {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    overflow: hidden; }
    .portoflio .projectCont .devProject .project {
      display: flex;
      justify-content: center;
      align-items: center; }
      .portoflio .projectCont .devProject .project img {
        width: 100%;
        -webkit-transition: .5s ease-in-out;
        transition: .5s ease-in-out; }
        .portoflio .projectCont .devProject .project img:hover {
          cursor: pointer;
          -webkit-filter: blur(8px);
                  filter: blur(8px); }
    .portoflio .projectCont .devProject .ProjectInfo {
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      z-index: 1;
      position: absolute;
      color: white;
      background: rgba(0, 0, 0, 0.377);
      width: 40%;
      -webkit-animation: scal 1s;
              animation: scal 1s; }
      .portoflio .projectCont .devProject .ProjectInfo div {
        display: flex;
        justify-content: space-between;
        width: 90%; }
        .portoflio .projectCont .devProject .ProjectInfo div p:nth-child(1) {
          font-weight: bold; }

@-webkit-keyframes scal {
  from {
    -webkit-transform: scaleY(0) translateY(-50%);
            transform: scaleY(0) translateY(-50%); }
  to {
    -webkit-transform: scaleY(1) translateY(0%);
            transform: scaleY(1) translateY(0%); } }

@keyframes scal {
  from {
    -webkit-transform: scaleY(0) translateY(-50%);
            transform: scaleY(0) translateY(-50%); }
  to {
    -webkit-transform: scaleY(1) translateY(0%);
            transform: scaleY(1) translateY(0%); } }

@media only screen and (max-width: 840px) {
  .portoflio {
    margin-top: 20%; }
    .portoflio h1 {
      font-size: 2rem; }
    .portoflio .CategoryCont {
      margin-bottom: 2em; }
      .portoflio .CategoryCont span {
        font-size: 1.2rem; }
    .portoflio .projectCont {
      display: grid;
      grid-template-columns: 1fr; }
      .portoflio .projectCont .devProject .ProjectInfo {
        width: 100%; }
        .portoflio .projectCont .devProject .ProjectInfo div {
          width: 95%; }
          .portoflio .projectCont .devProject .ProjectInfo div p {
            margin-bottom: .5em; } }

