.portoflio{
    margin-top: 10%;
    .CategoryCont{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5%;
        span{
            font-size: 2rem;
            border-radius: 50%;
            &:hover{
                
                cursor: pointer;
            }
            &:visited{
                background: #78909C ;
            }
            &:active{
                background: #78909C ;
            }
        
            p{
                padding: .5em .7em;
                margin: 0%;
            }
        }
    }
    
    .projectCont{
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-content: center;
        overflow: hidden;
        .devProject{
            .project{
                display: flex;
                justify-content: center;
                align-items: center;
                img{
                    width: 100%;
                    transition:  .5s ease-in-out;
                    &:hover{
                        cursor: pointer;
                        filter: blur(8px);
                    }
                }
        
            }
         .ProjectInfo{
             text-align: center;
             display: flex;
             flex-direction: column;
             justify-content: center;
             align-items: center;
             z-index: 1;
             position: absolute;
             color: white;
             background: rgba(0, 0, 0, 0.377);
             width: 40%;
             animation: scal 1s; 
             div{
                  display: flex;
                  justify-content: space-between;
                  width: 90%;  
                  p:nth-child(1){
                      font-weight: bold;
                  }
                }
         }
     
        }
    }
}



@keyframes scal {
    from{transform: scaleY(0) translateY(-50%)
  }
    to{transform: scaleY(1) translateY(0%)}
}

@media only screen and (max-width: 840px){
     .portoflio{
         margin-top: 20%;
         h1{
             font-size: 2rem;
         }
         .CategoryCont{
             margin-bottom: 2em;
             span{
                 font-size: 1.2rem;
             }
         }
         .projectCont{
             display: grid;
             grid-template-columns: 1fr;
             .devProject{
                .ProjectInfo{
                    width: 100%;
                    div{
                        width: 95%;
                        p{
                            margin-bottom: .5em;
                        }
                    }
                }
             }
           
         }
     }
}